body {
  font-family: "PT Serif", serif;
  font-size: 14px;
  margin: 20px;
  padding: 20px;
  background-color: whitesmoke;
}
body * {
  box-sizing: border-box;
}
body *#myBooks {
  text-align: center;
  width: 100%;
}
body *#myBooks .title {
  border-radius: auto;
  width: 100%;
  border: 1px gray solid;
  background-color: #cfd4d8;
  box-shadow: 9px 9px 11px gray;
}
body *#myBooks hr {
  margin-top: 20px;
}
body *#myBooks button {
  background-color: #2d9281;
  color: #eee2e2;
  font-size: 1.2rem;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  padding-right: 0.8rem;
  padding-left: 0.8rem;
}
body *#myBooks #newBook {
  display: none;
  text-align: left;
  width: 100%;
  margin: auto;
}
body *#myBooks #newBook label {
  width: 100%;
  margin-top: 20px;
  display: inline-block;
  text-align: left;
}
body *#myBooks #newBook label input {
  display: block;
  flex-direction: row;
  width: 100%;
}
body *#myBooks #newBook #search {
  color: white;
  border: lime;
  margin-top: 20px;
  width: 150px;
  height: 40px;
  font-size: 15px;
}
body *#myBooks #newBook #cancel {
  color: white;
  border: lime;
  font-size: 15px;
  width: 150px;
  height: 40px;
  display: inline-block;
  margin-top: 20px;
  background-color: #db3d3d;
}
body #result {
  display: none;
}
body #box {
  display: flex;
  flex-wrap: wrap;
  height: 10%;
}
body #rcherche {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}
body #container {
  padding: 10px;
  float: left;
  border: solid 2px black;
  width: 44%;
  height: 350px;
  margin: 5px;
  text-align: justify;
  overflow: auto;
}

